import React, { useState } from 'react';
import { BiSearchAlt } from "react-icons/bi";
import { Select, Tag } from 'antd';
import { TagGroup } from '../tagGroup';
const options = [
    {
        value: 'gold',
    },
    {
        value: 'lime',
    },
    {
        value: 'green',
    },
    {
        value: 'cyan',
    },
];
const tagRender = (props) => {
    const { label, value, closable, onClose } = props;


    const onPreventMouseDown = (event) => {
        event.preventDefault();
        event.stopPropagation();
    };
    return (
        <Tag
            color={value}
            onMouseDown={onPreventMouseDown}
            closable={closable}
            onClose={onClose}
            style={{
                marginInlineEnd: 4,
            }}
        >
            {label}
        </Tag>
    );
};
const SearchBox = () => {
    const [input, setInput] = useState("");
    const [tags, setTags] = useState(['＃2024 - SS', 'First', 'Collection', '#DRESS', '#JAPA']);
    return (
        <>
            <div className="p-4 flex relative bg-gray-50">
                <div className="w-full mx-auto">
                    <div className="flex items-center py-2 px-3 bg-gray-50 rounded-lg dark:bg-gray-700">
                        <div className="flex flex-col">
                        </div>
                        <input value={input}
                            onChange={(e) => setInput(e.target.value)}
                            className="block sm:mx-20 mx-2 p-2.5 w-full rounded-full border px-5" />
                        <BiSearchAlt className="relative sm:right-32 right-12 w-7 h-7" />
                    </div>
                    <div className="sm:ml-20 lg:ml-32 ml-2">
                        {tags.length && <TagGroup tags={tags} />}
                    </div>
                </div>
            </div>

        </>

    )
};
export default SearchBox;