import React, { useState } from "react";

import { useNavigate } from 'react-router-dom';
import { Breadcrumb } from 'antd';

import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import SearchIcon from '@mui/icons-material/Search';

import { TagGroup } from "../../components/common/tagGroup";
import { creatorData } from "../../constant/creator_data";
import { PaginationButtons } from "../../components/common/pagination";

import image91 from "../../assets/images/image 91.png";
import SearchBox from "../../components/common/searchBox";


function Creatorsearch() {
    const [age, setAge] = useState('');

    const navigate = useNavigate();

    const handleChange = (event) => {
        setAge(event.target.value);
    };

    return (
        <>
            {/* <Header /> */}
            <section id="main-section">
                <div className="py-12">
                    <Breadcrumb
                        className="p-5"
                        separator=">"
                        items={[
                            {
                                title: 'TOP_PAGE',
                                href: '/'
                            },
                            {
                                title: 'CREATEER_SEARCH',
                            },
                        ]}
                    />

                    <div className="border border-gray-500 py-[50px]">
                        <p className="text-[15px]">CREATEER SEARCH</p>
                        <div className="text-center">
                            <SearchBox />
                            <div className="flex items-center justify-center gap-2 w-2/5 flex-wrap m-0 relative left-0 right-0 ml-auto mr-auto">
                            </div>
                        </div>
                    </div>
                    <div className="w-1/3 cart-bar">
                        <p>#DESIGNER</p>
                    </div>
                    <div className="mt-16 flex items-center gap-7 flex-wrap justify-center bg-gray-400 p-10">
                        {creatorData.map((creator, index) => (
                            <div key={index} onClick={() => { navigate("/designer/profile") }} id="card" className="w-52 shadow-md shadow-gray-600 bg-white">
                                <div className="relative">
                                    <img className="w-full h-44" src={creator.mainImage} alt="main" />
                                    <p className="absolute top-2 left-4 bg-gray-700 text-white font-bold rounded-lg px-10 py-[1px]">{creator.status}</p>
                                    <p className="bg-blue-800 text-white absolute rounded-lg text-xs top-9 left-4 px-3 py-1">{creator.availability}</p>
                                </div>
                                <img className="w-20 h-20 rounded-full shadow-md shadow-gray-600 mt-[-40px] ml-16 absolute" src={creator.userImage} alt="user" />
                                <h1 className="pt-16 text-center text-[15px] font-bold">{creator.username}</h1>
                                <p className="pb-2 pt-5 text-center text-[7px] font-semibold">{creator.description}</p>
                            </div>
                        ))}
                    </div>
                </div>
                <PaginationButtons />
                {/* <div id="pagination" className="pagination-section mb-10">
                    <button className="pagination-jp-btn px-4 py-2">最初へ</button>
                    <button className="pagination-jp-btn"><ChevronLeftIcon /></button>
                    <button className="pagination-jp-btn px-4 py-2">1</button>
                    <button className="pagination-jp-btn"><ChevronRightIcon /></button>
                    <button className="pagination-jp-btn px-4 py-2">最後へ</button>
                </div> */}
            </section>
        </>
    );
}

export default Creatorsearch;
